import "./style.css"
import { Panel } from '../panel'

interface Options {
    containerId: string,
    title: string,
    content: string,
    expandContent?: string | null,
    onClickButton?: Function | null,
    buttonTitle?: string,
}

export class Modal {

    get title() { return this.panel.title }
    set title(v) {
        this.panel.title = v
    }

    // html element
    private readonly _rootElm: HTMLElement

    // subcomponent
    readonly panel: Panel

    constructor(private readonly _options: Options) {
        const {
            containerId,
            title,
            content,
            expandContent = null,
            onClickButton = null,
            buttonTitle = '',
        } = _options

        this._rootElm = document.querySelector(`#${containerId}`)
        this._rootElm.classList.add('modal')

        // add all the staffs
        this._rootElm.insertAdjacentHTML('beforeend', `
            <div class="background"></div>
            <div class="content">
                <div id="${containerId}_panelSlot"> </div>
            </div>
        `)

        this.panel = new Panel({
            containerId: `${containerId}_panelSlot`,
            title,
            content,
            expandContent,
            onClickButton,
            buttonTitle,
            center: true,
        })

    }

    appendContent(content) {
        this.panel.appendContent(content)
    }

    show() {
        this._rootElm.classList.add('active')
    }

    hide() {
        this._rootElm.classList.remove('active')
    }

    hideButton() {
        this.panel.hideButton()
    }

    showButton() {
        this.panel.showButton()
    }
} // close class Modal