interface Options {
    svg: SVGSVGElement
    closePathDistance?: number
    onStart: () => void;
    onEnd: (points: Array<[number, number]>) => void;
}


export class Lasso {

    svg: SVGSVGElement
    g: SVGGElement
    dyn_path: SVGPathElement
    close_path: SVGPathElement
    origin_node: SVGCircleElement

    constructor(options: Options) {
        const {
            svg,
            onStart,
            onEnd
        } = options

        this.svg = svg

        // add a new group for the lasso
        const g = document.createElementNS('http://www.w3.org/2000/svg', 'g')
        g.setAttribute('class', 'lasso')
        svg.appendChild(g)
        this.g = g

        // add the drawn path for the lasso
        this.dyn_path = document.createElementNS('http://www.w3.org/2000/svg', 'path')
        this.dyn_path.setAttribute('class', 'drawn')
        g.appendChild(this.dyn_path)

        // add a closed path
        this.close_path = document.createElementNS('http://www.w3.org/2000/svg', 'path')
        this.close_path.setAttribute('class', 'loop_close')
        g.appendChild(this.close_path)

        // add an origin node
        this.origin_node = document.createElementNS('http://www.w3.org/2000/svg', 'circle')
        this.origin_node.setAttribute('class', 'origin')
        g.appendChild(this.origin_node)

        this.mountPathDrawing(onStart, onEnd)
        this.mountPathDraging()
    }

    mountPathDrawing(onStart: Function, onEnd: Function) {
        let tpath: string;
        let torigin: [number, number];
        let drawnCoords: Array<[number, number]>;

        const closePathDistance = 100
        let isDownSvg = false
        let isPathClosed = false;
        const svgStart = (e: MouseEvent) => {
            if (e.which !== 1) return
            if (e.target !== e.currentTarget) return

            isDownSvg = true
            this.cleanDrawing()

            // Init coordinates
            drawnCoords = [];
            // Initialize paths
            tpath = "";
            this.dyn_path.setAttribute('d', '')
            this.close_path.setAttribute('d', '')
            onStart()
        }
        const svgEnd = (e: MouseEvent) => {
            if (e.which !== 1) return
            if (!isDownSvg) return

            isDownSvg = false

            if (tpath !== '') {
                onEnd(drawnCoords)
            }
        }
        const svgMove = (e: MouseEvent) => {
            if (!isDownSvg) return
            // Get mouse position within body, used for calculations
            const dim = this.svg.getBoundingClientRect()
            const tx = e.clientX - dim.left;
            const ty = e.clientY - dim.top;

            // Initialize the path or add the latest point to it
            if (tpath === "") {
                tpath = tpath + "M " + tx + " " + ty;
                // origin = [x, y];
                torigin = [tx, ty];
                // Draw origin node
                this.origin_node.setAttribute('cx', `${tx}`)
                this.origin_node.setAttribute('cy', `${ty}`)
                this.origin_node.setAttribute('r', '7')
                this.origin_node.setAttribute('display', '')
            } else {
                tpath = tpath + " L " + tx + " " + ty;
            }

            drawnCoords.push([tx, ty]);

            // Calculate the current distance from the lasso origin
            const distance = Math.sqrt(Math.pow(tx - torigin[0], 2) + Math.pow(ty - torigin[1], 2));

            // Set the closed path line
            const close_draw_path = "M " + tx + " " + ty + " L " + torigin[0] + " " + torigin[1];

            // Draw the lines
            this.dyn_path.setAttribute("d", tpath);
            this.close_path.setAttribute("d", close_draw_path);

            // Check if the path is closed
            isPathClosed = distance <= closePathDistance ? true : false;

            // If within the closed path distance parameter, show the closed path. otherwise, hide it
            if (isPathClosed) {
                this.close_path.setAttribute("display", '');
            } else {
                this.close_path.setAttribute("display", "none");
            }
            // on.draw();
        }

        this.svg.addEventListener('mousedown', svgStart)
        this.svg.addEventListener('mousemove', svgMove)
        this.svg.addEventListener('mouseup', svgEnd)
        this.svg.addEventListener('mouseleave', svgEnd)
    }

    cleanDrawing() {
        this.g.setAttribute('transform', '')
        this.dyn_path.setAttribute("d", '');
        this.close_path.setAttribute("d", '');
        this.origin_node.setAttribute("display", "none");
    }

    mountPathDraging() {
        let isDownDrawnPath = false
        let porigin: [number, number] | null
        const drawnPathStart = (e: MouseEvent) => {
            if (e.which !== 1) return
            if (e.target !== e.currentTarget) return
            isDownDrawnPath = true
            porigin = null
        }
        const drawPathMove = (e: MouseEvent) => {
            if (!isDownDrawnPath) return
            const dim = this.svg.getBoundingClientRect()
            const tx = e.clientX - dim.left;
            const ty = e.clientY - dim.top;
            if (!porigin) {
                porigin = [tx, ty]
            } else {
                this.g.setAttribute('transform', `translate(${tx - porigin[0]}, ${ty - porigin[1]})`)
            }
        }
        const drawPathEnd = (e: MouseEvent) => {
            if (e.which !== 1) return
            if (!isDownDrawnPath) return
            isDownDrawnPath = false

        }

        this.dyn_path.addEventListener('mousedown', drawnPathStart)
        this.dyn_path.addEventListener('mousemove', drawPathMove)
        this.dyn_path.addEventListener('mouseup', drawPathEnd)
        this.dyn_path.addEventListener('mouseleave', drawPathEnd)
    }


}